.for-artist{
  margin-bottom: 50vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  &__wrapper{
    width: 70%;
  }
  &__header{
    font-size: 50px;
  }
  &__text{
    font-size: 30px;
    margin-bottom: 3vh;
  }
  &__link{
    color: #000;
    text-decoration: none;
    cursor: pointer;
    border-bottom: 2px solid black;
  }
}

.rules{
  &__description{
    color: #9E9B9B;
    font-size: 30px;
    margin-bottom: 2vh;
  }
  &__header{
    font-size: 50px;
    color: #9E9B9B;
  }
}