.item{
  &__picture{
    height: 25vw;
    width: 25vw;
    object-fit:cover;
    border-radius: 13.5vw;
    margin-top: 1vw;
  }
  &__wrapper{
    width: 27vw;
    height: 27vw;
    background: #fff;
    border-radius: 13.5vw;
  }
  &__name{
    font-size: 32px;
    font-weight: bold;
  }
  &__description{
    font-size: 20px;
    max-width: 20vw;
    margin: 0 auto;
  }
}

.team{
  &__list{
    position: relative;
    display: flex;
    list-style: none;
    transition: 0.6s;
  }
  &__item{
    margin-right: 4vw;
    text-align: center;
  }
  &__wrapper{
    position: relative;
    overflow: hidden;
    margin-bottom: 50vh;
  }
}

.line{
  position: absolute;
  &__picture{
    width: 27vw;
    height: 27vw;
  }
}

.arrow-button__team{
  border: none;
  background: transparent;
  position: absolute;
  bottom: 0;
  &.right{
    right: 33%;
    top: 81%;
  }
  &.left{
    left: 31%;
    top: 81%;
  }
}

