.news{
  &__line{
    position: absolute;
    z-index: -1;
    top: -3vh;
    left: 9vw;
    width: 30vw;
  }
  &__item{
    display: flex;
    margin-bottom: 2%;
    > .news__text{
      border-bottom: 3px solid black;
    }
    &:last-child{
      > .news__text{
        border-bottom: none;
      }
    }
  }
  &__title{
    font-size: 45px;
    font-weight: bold;
  }
  &__description{
    font-size: 30px;
  }
  &__link{
    text-decoration: none;
    color: #9E9B9B;
  }
  &__info{
    color: #9E9B9B;
    font-weight: lighter;
    font-size: 35px;
    display: flex;
    justify-content: space-between;
  }
  &__picture{
    margin-right: 4%;
    width: 40%;
  }
  &__list{
    width: 75%;
  }
  &__wrapper{
    justify-content: center;
    display: flex;
    margin-bottom: 11vh;
    position: relative;
  }
}

