body{
  margin: 0;
  overflow-x: hidden;
}

.container{
  width: 90%;
  margin: 0 auto;
  &.registration{
    display: flex;
    justify-content: center;
    position: relative;
  }
}

.logo{
  margin-top: 5%;
  margin-bottom: 14.8%;
}

.registration{
  position: relative;
  margin-bottom: 10vh;
  &__line{
    position: absolute;
    top: 5vh;
    left: 25vw;
    width: 50vw;
  }
}

.main{
  &__slice{
    position: absolute;
    top: 0;
    right: 0;
    z-index: -1;
  }
  &__background{
    position: absolute;
    top: -20vh;
    z-index: -1;
  }
  &__page{
    display: block;
  }
  &__heading{
    text-transform: uppercase;
    font-size: 55px;
    margin: 0;
    margin-bottom: 5vh;
    text-align: center;
    &.white{
      color: white;
    }
    &.black{
      color: black;
    }
  }
  &__about{
    display: block;
  }
  &__logo{
    margin-left: -100px;
  }
}

.footer{
  position: relative;
  &__background{
    position: absolute;
    z-index: -1;
    top: -100vh;
  }
}

.about-project{
  &__description{
    color: white;
    font-size: 35px;
    margin-bottom: 4%;
    &.bold {
      font-weight: bold;
    }
  }
  &__button{
    display: block;
    cursor: pointer;
    color: black;
    height: 74px;
    border: white;
    border-radius: 37px;
    background: white;
    width: 33%;
    font-size: 35px;
    margin-bottom: 18%;
  }
}