.slider{
  &__arts{
    display: flex;
    list-style: none;
    position: relative;
    transition: 0.6s;
  }
  &__wrapper{
    position: relative;
    overflow: hidden;
    margin-bottom: 20vh;
  }
  &__item{
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__picture{
    width: 27vw;
  }
}

.arrow-button{
  border: none;
  background: transparent;
  position: absolute;
  bottom: 0;
  &.right{
    right: 11%;
  }
  &.left{
    left: 11%
  }
}