.form{
  width: 40vw;
  display: flex;
  align-items: center;
  flex-direction: column;
  background: #fff;
  margin-bottom: 20vh;
  filter: drop-shadow(0 4px 10px #666);
  padding: 2vw 2vh;
  &__consent{
    width: 45%;
    text-align: center;
    color: #9E9B9B;
  }
  &__input{
    padding: 0;
    line-height: 1;
    font-size: 18px;
    outline: none;
    border: none;
    border-bottom: 2px solid black;
    &.date{
      width: 12%;
      padding: 0 10px;
    }
  }
  &__registration{
    flex: 1;
    display: flex;
    justify-content: space-between;
  }
  &__column{
    width: 45%;
    display: flex;
    flex-direction: column;
  }
}

.date-area{
  display: flex;
  position: relative;
  &::after{
    content: '';
    width: 3px;
    height: 15px;
    position: absolute;
    background: #000;
    transform: rotate(20deg);
    top:26%;
    left: 15%;
  }
  &::before{
    content: '';
    width: 3px;
    height: 15px;
    position: absolute;
    background: #000;
    transform: rotate(20deg);
    top: 26%;
    left: 33%;
  }
}


.input{
  &__wrapper{
    position: relative;
  }
  &__hidden{
    opacity: 0;
    visibility: hidden;
    position: absolute;
  }

  &__label{
    margin-bottom: 5vh;
    font-weight: bold;
    &.date{
      position: relative;
      &::after{
        content: '';
        width: 3px;
        height: 15px;
        position: absolute;
        background: #000;
        transform: rotate(20deg);
        top: -83%;
        left: 15%;
      }
      &::before{
        content: '';
        width: 3px;
        height: 15px;
        position: absolute;
        background: #000;
        transform: rotate(20deg);
        top: -83%;
        left: 33%;
      }
    }
    &.file{
      cursor: pointer;
    }
    &.photo{
      margin-bottom: 1vh;
    }
  }
  &__file-wrapper{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 4vw;
    height: 4vh;
    border: 2px solid black;
    border-radius: 12px;
  }
  &__file-picture{
    display: block;
    width: 13px;
  }
}

.registration__button{
  font-size: 25px;
  font-weight: bold;
  border: 2px solid black;
  border-radius: 10px;
  cursor: pointer;
  background: transparent;
  padding: 2vh 3vw;
  text-transform: uppercase;
  margin-bottom: 1vh;
}