.arts{
  &__list{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    list-style: none;
  }
  &__item{
    display: flex;
  }
  &__row{
    display: flex;
    height: 50vh;
  }
  &__column{
    width: 28.125vw;
    height: 100%;
    &.left{
      margin-right: 3vw;
    }
    &.right{
      width: 41.5625vw;
      height: 50vh;
    }
  }
  &__picture{
    &-one{
      height: 40vh;
      width: 28.125vw;
    }
    &-two{
      width: 22.625vw;
      height: 50vh;
    }
    &-three{
      height: 50vh;
      width: 18.9375vw;
    }
    &-four{
      height: 50vh;
      width: 28.125vw;
    }
    &-five{
      height: 40vh;
      width: 41.5625vw;
    }
  }
}

.container{
  &.gallery{
    width: 70vw;
  }
}

.footer__background{
  &.gallery{
    margin-top: 30vh;
  }
}

.gallery{
  &__heading{
    text-transform: uppercase;
    font-size: 55px;
    &.white{
      color: white;
    }
  }
}

.type{
  &__list{
    display: flex;
    list-style: none;
  }
  &__item{
    margin-right: 1vw;
  }
  &__button{
    cursor: pointer;
    padding: 20px;
    font-weight: bold;
    font-size: 20px;
    outline: none;
    width: 15vw;
    border: 2px solid white;
    border-radius: 50px;
    color: white;
    background: transparent;
    -webkit-backdrop-filter: blur(5px);
    backdrop-filter: blur(5px);
    &.active{
      background: white;
      color: black;
    }
  }
}